.try-again-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.try-again-container > h1 {
  text-decoration: underline;
  margin: 0;
  font-size: 36px;
  margin-bottom: 20px;
}

.result-container {
  font-size: 30px;
  margin: 7px;
  font-family: "Roboto";
}

.end-button {
  position: relative;
  margin: 12px;
  border: none;
  border-radius: 4px;
  min-width: 64px;
  padding: 2px 16px;
  vertical-align: middle;
  text-align: center;
  color: #fff;
  font-size: 18px;
  font-size: 500;
  line-height: 36px;
  cursor: pointer;
  transition: background-color 0.2s;
}
.start-again-btn {
  background-color: #3e4248;
}

.start-again-btn:hover,
.start-again-btn:focus {
  background-color: #808080;
}
.share-btn {
  background-color: #3b5998;
}

.share-btn:hover,
.share-btn:focus {
  background-color: #1da1f2;
}
.tweet-button {
  background-color: #1da1c3;
}

.tweet-button:hover,
.tweet-button:focus {
  background-color: #407b91;
}
